@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
  overflow-x: hidden!important;
}
#root{
  overflow-x: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Tilt{
	  background:linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
	  cursor: pointer;
	  overflow-x: hidden;

}
.pp{
	font-family: 'Raleway',sans-serif;
	font-weight: bold;
}
.detect{
	background-color: #FF5EDF!important;
	border-color: transparent;
	color: #fff;
}
.detect:hover {
	background-color: #f450d4!important;
	border-color: transparent!important;
	color: #FFF;
}
.container_detect{

}
.particles{
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	z-index: -1;
}
body{
	overflow-x: hidden;
}
.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
	overflow-x: hidden;
}
.bordery{
	background-color: rgba(0,0,0,.1);
	color:black;
	font-size: 1.1rem;
	width: 50%;
	box-shadow: 1px 1px 5px rgba(0,0,0,.5);
	border-radius: .5rem;
}
.border form {color:#000;
}
.bordery ::-webkit-input-placeholder {
	color: #000;
}
.bordery :-ms-input-placeholder {
	color: #000;
}
.bordery ::-ms-input-placeholder {
	color: #000;
}
.bordery ::placeholder {
	color: #000;
}
.bordery p {color:#fff;
}
.bordery p:hover {
	text-decoration: none;
	color:rgba(0,0,0,.7);
	cursor: pointer;}
.sign_in {
background-color: rgba(0,0,0,.1);
padding: 1rem;

}
.text_field {
	color: #fff!important;
	border-color: #fff;
	background-color: #fff;
}
@media only screen and (max-width: 572px) {
	.bordery{ padding-top:2rem;
	padding-bottom: 2rem; 
	background-color: rgba(0,0,0,.1);
	color:black;
	font-size: 1.1rem;
	width: 90%;
	box-shadow: 1px 1px 5px rgba(0,0,0,.5);
	border-radius: .5rem;
}
}
